import { request } from 'utils';

const prefix = '/api/students';

export const getPolicyStudents = ({
  studyFieldId,
  trainTypeId,
  facultyId,
  courseId,
  classId,
  semesterId,
  yearEduId,
  policyObjectId,
  studentCode,
  fullName,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: `/Student/GetPolicyStudent`,
    method: 'GET',
    params: {
      studyFieldId,
      trainTypeId,
      facultyId,
      courseId,
      classId,
      semesterId,
      yearEduId,
      policyObjectId,
      studentCode,
      fullName,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const createPolicyStudents = data =>
  request({
    prefix,
    url: `/Student/CreatePolicyStudent`,
    data,
    method: 'POST',
  });

export const deletePolicyStudents = data =>
  request({
    prefix,
    url: `/Student/DeletePolicyStudent`,
    data,
    method: 'DELETE',
  });

export const getEnrollSolutionsById = id =>
  request({
    prefix,
    url: `/EnrollSolution/${id}`,
    method: 'GET',
  });

export const updateEnrollSolutionStudents = ({
  id,
  label,
  courseId,
  areaids,
  minTotalPoint,
  minPointPerSubject,
  noOfStudent,
  reviewTypeId,
}) =>
  request({
    prefix,
    url: `/EnrollSolution/${id}`,
    data: {
      id,
      label,
      courseId,
      areaids,
      minTotalPoint,
      minPointPerSubject,
      noOfStudent,
      reviewTypeId,
    },
    method: 'PUT',
  });



