import React, { Component } from 'react';
import { Row, Col, Form, Input } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { BtnSearch, BtnClear } from 'components/common/button';
import { getQueryParams, udpateQueryParams } from 'utils/urlUtil';
import { getPolicyObjects } from 'services/policyObjectForPolicyStudent';
import { SelectAjax } from 'components/common/index';
import { requiredRule } from 'utils/validateRules';

@Form.create({ name: 'formSearch' })
class SearchBar extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    udpateQueryParams();
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [requiredRule],
      },
      facultyId: {
        rules: [requiredRule],
      },
      courseId: {
        rules: [requiredRule],
      },
      studyFieldId: {
        rules: [requiredRule],
      },
      yearEduId: {
        rules: [requiredRule],
      },
      semesterId: {
        rules: [requiredRule],
      },
    };
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <YearSemesterInput form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <Col span={6}>
            <Form.Item label="ĐTCS">
              {getFieldDecorator(`policyObjectId`, {})(<SelectAjax service={getPolicyObjects} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Mã SV">{getFieldDecorator('studentCode', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Họ tên">{getFieldDecorator('fullName', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



